body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  background: #111;
  background-repeat: no-repeat;
}

.signupSection {
background-repeat: no-repeat;
background-position-y: bottom;
position: absolute;
top: 50%;
left: 50%;
transform: translate(-50%,-50%);
width: 800px;
height: 550px;
text-align: center;
display: flex;
color: white;
box-shadow: 3px 10px 20px 5px rgba(0,0,0,.5);
}

.info{
  width: 45%;
  background: rgba(20,20,20,.8);
  padding: 5px 0;
  border-right: 5px solid rgba(30,30,30,.8)
}
.info h2 {
  padding-top: 20px;
  font-weight: 300;
  font-size: 30px;
}
.info p {
  margin-top: 60px;
  font-size: 20px;
}

.fa {
  margin: 30px 0;
  font-size: 120px;
  padding: 20px 0;
  color: white;
}

.formSend {
  color: #f2f2f2;
}

.signupForm {
  width: 70%;
  padding: 30px 0;
  background: rgba(20,40,40,.8);
  transition: .2s;
}
.signupForm h2 {
  font-weight: 300;
  font-size: 30px;
}

label {
  display: block;
}

input {
  margin: 15px 0;
  font-size: 16px;
  padding: 10px;
  width: 250px;
  border: 1px solid rgba(10,180,180,1);
  border-top: none;
  border-left: none;
  border-right: none;
  background: rgba(20,20,20,.2);
  color: #f2f2f2;
  outline: none;
}

label:last-of-type{
  font-size: 10px;
  color: #f2f2f2;
  display: inline;
}

button {
  border: 1px solid rgba(10,180,180,1);
  background: rgba(20,20,20,.6);
  font-size: 20px;
  padding: 10px 20px;
  text-transform: uppercase;
  color: #f2f2f2;
  margin-top: 60px;
  cursor: pointer;
  transition: .4s;
}
button:hover {
  background: rgba(20,20,20,.8);
  padding: 10px 50px;
}

span {
  margin-top: -15px;
  max-width: 250px;
  text-align: center;
  margin-left: 25%;
  display: block;
  color:rgba(255,110,110,1);
  font-size: 10px;
}

input[type='checkbox'] {
  margin: 0;
  margin-top: 10px;
  padding: 0;
}
.terms {
  padding-bottom: 15px;
  color: #f2f2f2;
  line-height: 12px;
  margin-right: 200px;
  margin-top: -15px;
  font-size: 12px;
 display: block;
 width: 250px;
}
